import FooterNavLink from "./FooterNavLink";

const FooterNav = () => {

    const nav = {
        "–î–ª—è –ø–æ—Å—Ç–∞–≤—â–∏–∫–æ–≤": "#",
        "–ù–∞—à–∏ –¥–æ–∫—É–º–µ–Ω—Ç—ã": "#",
        "–û –ø—Ä–æ–∏–∑–≤–æ–¥—Å—Ç–≤–µ": "#",
        "–≠–∫–æ–ª–æ–≥–∏—á–µ—Å–∫–∏–µ —Å—Ç–∞–Ω–¥–∞—Ä—Ç—ã": "#",
    };

    return (
        <nav className="footer__nav">
                <ul className="footer__nav-links">
                    {Object.entries(nav).map(([key, value], index) => {
                        return (
                            <FooterNavLink
                                key={index}
                                link={value}
                                text={key}
                            />
                        );
                    })}
                </ul>
            </nav>
    )
}

export default FooterNav;