import React from "react";
import { useDispatch } from "react-redux";
import { addProductToCart } from "../../redux/cartSlice";

const ProductsItem = ({ price, image, description, name, bestseller }) => {
    const dispatch = useDispatch();

    const handleClick = ({ currentTarget }) => {
        dispatch(addProductToCart(currentTarget.id));
    };

    return (
        <div
            className={
                bestseller
                    ? "products__item bestsellers__item"
                    : "products__item"
            }
            id={name}
            onClick={handleClick}
        >
            <img className="products__image" src={image} alt="products item" />
            <p className="products__price">
                {price} &#8381;<span>/–∫–≥</span>
            </p>
            <p className="products__description">{description}</p>
            <p className="products__item-hint">
                –ö–ª–∏–∫–Ω–∏—Ç–µ, —á—Ç–æ–±—ã –¥–æ–±–∞–≤–∏—Ç—å –≤ –∫–æ—Ä–∑–∏–Ω—É.
            </p>
        </div>
    );
};

export default ProductsItem;
