import SortFillersItem from "./SortFillersItem";

const SortFillers = ({ fillers, setFillers }) => {
    const data = [
        { value: "chocolate", name: "–®–æ–∫–æ–ª–∞–¥–Ω—ã–µ" },
        { value: "powder", name: "–°–∞—Ö–∞—Ä–Ω—ã–µ –ø—Ä–∏—Å—ã–ø–∫–∏" },
        { value: "fruit", name: "–§—Ä—É–∫—Ç—ã" },
        { value: "syrup", name: "–°–∏—Ä–æ–ø—ã" },
        { value: "jam", name: "–î–∂–µ–º—ã" },
    ];

    const handleChange = ({ target }) => {
        const arr = [...fillers];

        if (arr.length === 0) {
            arr.push(target.value);
        } else {
            const index = arr.findIndex((filler) => filler === target.value);
            index === -1 ? arr.push(target.value) : arr.splice(index, 1);
        }

        setFillers(arr);
    };

    return (
        <fieldset className="sort__fillers sort__field">
            <legend className="sort__title">–ù–∞–ø–æ–ª–Ω–∏—Ç–µ–ª–∏:</legend>
            {data.map((item, index) => {
                return (
                    <SortFillersItem
                        key={index}
                        handle={handleChange}
                        value={item.value}
                        name={item.name}
                    />
                );
            })}
        </fieldset>
    );
};

export default SortFillers;
