import React, { useState } from "react";
import Button from "../Button";
import { useDispatch } from "react-redux";
import { toggleNotification } from "../../redux/notificationSlice";

const Subscription = () => {
    const dispatch = useDispatch();

    const [formValue, setFormValue] = useState("");

    const handleClick = (e) => {
        e.preventDefault();
        dispatch(toggleNotification(true));
        setFormValue("");
    };

    return (
        <article className="subscription">
            <div className="subscription__container">
                <p className="subscription__text">
                    –ü–æ–¥–ø–∏—à–∏—Ç–µ—Å—å –Ω–∞ –Ω–∞—à—É —Å–ª–∞–¥–∫—É—é —Ä–∞—Å—Å—ã–ª–∫—É –∏ –±—É–¥–µ—Ç–µ –≤—Å–µ–≥–¥–∞ –≤ –∫—É—Ä—Å–µ
                    –≤—Å–µ–≥–æ —Å–∞–º–æ–≥–æ –≤–∫—É—Å–Ω–æ–≥–æ, —á—Ç–æ —É –Ω–∞—Å –ø—Ä–æ–∏—Å—Ö–æ–¥–∏—Ç. –û–±–µ—â–∞–µ–º –Ω–µ
                    —Å–ø–∞–º–∏—Ç—å –∏ –Ω–µ —Å–ª–∞—Ç—å –≤—Å—è–∫–æ–π –Ω–µ –Ω—É–∂–Ω–æ–π –µ—Ä—É–Ω–¥—ã. –ß–µ—Å—Ç–Ω–æ =){" "}
                </p>
                <form className="subscription__form">
                    <input
                        className="subscription__mail"
                        type="email"
                        placeholder="–≠–ª–µ–∫—Ç—Ä–æ–Ω–Ω–∞—è –ø–æ—á—Ç–∞"
                        value={formValue}
                        onChange={({ target }) => setFormValue(target.value)}
                    />
                    <Button text={"–û—Ç–ø—Ä–∞–≤–∏—Ç—å"} handleClick={handleClick} />
                </form>
            </div>
        </article>
    );
};

export default Subscription;
